.input {
	-webkit-transition: width .35s linear;
	-o-transition: width .35s linear;
	transition: width .35s linear;
	outline: none;
	border: none;
	border-radius: 4px;
	padding: 10px;
	font-size: 20px;
	width: 150px;
	background-color: #ddd;
}

.input-focused {
	width: 240px;
}



